*{
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  background-color: #39a88c;
  color: rgb(0, 0, 0);
}  

.header{
  background-color: rgb(0, 0, 0);
  color: white;
}

.main{
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 14px;
}

.movies{
  background-color: rgb(171, 201, 22);
  color: black;
  border: 1px solid rgb(3, 3, 3);
  padding: 3px;
  font-weight: 900;
  text-align: center;
  width: 22%;
  margin: 10px;
}

.movies img{
  width: 100%;
  height: 300px;
}
